import * as React from "react";
import "./App.css";
import EDU from "./Js/educationScript";
import Extra from "./extra";

import {Routes,Route,Link} from "react-router-dom";

function Education() {
  return (
    <>
      <nav class="navbar">
        <h2>My Resume</h2>
        <input type="checkbox" id="box" />
        <label for="box" class="checkbtn">
          &#9776;
        </label>
        <ul class="links">
          <li><Link to="/react-project/">Erik Norell</Link></li>
          <li><Link to="/react-project/experience/">Experience</Link></li>
          <li class="active"><Link to="/react-project/education/">Education</Link></li>
          <li><Link to="/react-project/projects/">Projects</Link></li>
          <li><Link to="/react-project/contact/">Contact</Link></li>
        </ul>
      </nav>
      <div class="content">
        <div class="content-text">
          <h1>My Education</h1>
          <div class="container">
            <EDU/>
          </div>

          <br />
        </div>
      </div>
      <Extra />
    </>
  );
}
export default Education;
